.secciones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px ;
}
/* Contenedor general */
.estudios-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

/* Imagen */
.estudios__imagen {
    width: 200px;
    height: 200px;
    border-radius: 5px;
    object-fit: cover;
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.6);
    transition: transform 0.4s ease;
}

.estudios__imagen:hover {
    transform: scale(1.05);
    box-shadow: 0px 8px 25px rgba(28, 228, 151, 0.5);
}

/* Información */
.informacion-estudios {
    flex: 1;
    max-width: 600px;
}

.informacion-estudios .titulo {
    font-size: 2.8rem;
    margin-bottom: 15px;
    color: #1ce497;
    text-shadow: 0px 2px 6px rgba(28, 228, 151, 0.6);
}

.informacion-estudios .institucion {
    font-size: 2rem;
    font-weight: 600;
    color: #aaa;
    margin-bottom: 8px;
}

.informacion-estudios .ciclo {
    font-size: 1.5rem;
    color: #ddd;
    margin-bottom: 20px;
}

.informacion-estudios .ciclo span {
    color: #1ce497;
    font-weight: bold;
}

.informacion-estudios .descripcion {
    font-size: 1.5rem;
    line-height: 1.6;
    color: #e5e5e5;
}


/* Responsive */
/* 📱 Responsive */
@media (max-width: 992px) {
    .secciones {
        grid-template-columns: repeat(2, 1fr); /* 2 columnas en tablets */
    }
}

@media (max-width: 600px) {
    .secciones {
        grid-template-columns: 1fr; /* 1 columna en celular */
    }
}